.details {

  &__title {
    margin-bottom: rem(48);
  }

  &__descriptions {
    @include grid;
    row-gap: rem(40);

    @media (min-width:$mobile) {
      row-gap: rem(48);
    }
  }

  &__description {
    grid-column: 1 / -1;
  }
}

.description {
  @include grid;
  row-gap: rem(16);

  &__link {
    position: relative;

    @include anyHoverImage(scale(1.2));

    &--wide {
      @media (min-width:$mobile) {
        grid-column: span 4;
      }

      @media (min-width:$tablet) {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;
      @media (min-width:$mobile) {
        grid-column: span 2;
      }

      @media (min-width:$tablet) {
        grid-column: span 4;
      }
    }

    &:active > img {
      transform: scale(1);
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__info {
    grid-column: span 2;

    @media (min-width:$mobile) {
      grid-column: span 6;
    }

    @media (min-width:$tablet) {
      grid-column: span 8;
    }
  }

  &__subtitle {
    &:not(:last-child) {
      margin-bottom: em(8, 24);
    }
  }

  &__button {
    grid-column: span 2;

    @media (min-width:$mobile) {
      grid-column: 2 / 6;
    }

    @media (min-width:$tablet) {
      grid-column: 5 / 9;
    }
  }
}
