.contacts {

  &__body {
    @include adaptiveValue("padding-bottom", 182, 120);
  }

  &__title {
    margin-bottom: rem(48);
    font-size: rem(32);
    font-weight: 600;
    line-height: 1;
    letter-spacing: rem(-1.5);
    color: $white;
    text-align: center;

    @media (min-width:$mobile) {
      font-size: rem(48);
    }
  }

  &__content {
    @include grid;
    row-gap: rem(48);
  }

  &__form {
    grid-column: span 2;

    @media (min-width:$mobile) {
      grid-column: span 3;
    }

    @media (min-width:$tablet) {
      grid-column: span 5;
    }
  }

  &__info {
    grid-column: span 2;

    @media (min-width:$tablet) {
      grid-column: 8 / -1;
    }
  }
}

.info-contacts {

  &__item {
    &:not(:last-child) {
      margin-bottom: rem(24);
    }
  }

  &__title {
    margin-bottom: rem(8);
    font-size: rem(14);
  }

  &__link {
    position: relative;
    font-weight: 500;
    @include hoverLinks($white);
  }
}
