.compare {

  &__title {
    @include adaptiveValue("margin-bottom", 112, 98);
  }

  &__cards {
    @include grid;
    @include adaptiveValue("row-gap", 110, 90);
  }

  &__card-product {
    grid-column: span 2;

    @media (min-width:$mobile) {
      grid-column: 2 / 6;
    }

    @media (min-width:$tablet) {
      grid-column: span 4;
    }
  }
}

.card-product {

  &__image {
    margin-bottom: rem(32);

    & img {
      width: 100%;
    }

    @include anyHoverImage(scale(1.05));

    &:active {
      & img {
        transform: scale(1);
      }
    }
  }

  &__title {
    margin-bottom: rem(8);
  }

  &__text {
    margin-bottom: rem(16);
  }
}
