$minWidth: 320;
$maxWidth: 1260;
$maxWidthContainer: 1020;

$vw: 100vw;

$containerPaddingTo: 40;
$containerPaddingFrom: 144;

@function getContainerPadding($vw) {
  $containerPadding: if($vw <= 767.98,
  $containerPaddingTo, $containerPaddingFrom);
  @return $containerPadding;
}

@function getContainerWidth($padding) {
  $containerWidth: $maxWidthContainer + $padding;
  @return $containerWidth;
}

$containerPadding: getContainerPadding($vw);

// Ширина спрацьовування першого брейкпоінту
$containerWidth: getContainerWidth($containerPadding);

$fontFamily: "Poppins", sans-serif;
$white: #fff;
$colorBg: #1d1d1d;
$menuBg: #c2c2c2;
$textForm: #7e7e83;
