.icon {
  display: block;
  @include square(rem(24));
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;

  @media (min-width:$mobile) {
    @include square(rem(32));
  }

  @include anyHover(transform, scale(1.2));

  &--phone {
    position: relative;
    background-image: url("../images/icons/phone.svg");

    &::before {
      content: "+1 234 555-55-55";
      position: absolute;
      top: 50%;
      width: max-content;
      color: #fff;
      font-weight: 700;
      font-size: rem(12);
      line-height: math.div(16, 12);
      letter-spacing: rem(2);
      text-transform: uppercase;
      padding: rem(10) rem(20);
      opacity: 0;
      transform: translate(-100%, -50%);
      pointer-events: none;
      transition: opacity 0.3s;
    }

    @media (any-hover: hover) {
      &:hover::before {
        opacity: 1;
        pointer-events: all;
      }
    }
  }

  &--menu {
    background-image: url("../images/icons/menu.svg");

    @media (any-hover: hover) {
      &:hover {
        background-image: url("../images/icons/menu-hover.svg");
      }
    }
  }

  &--menu-close {
    position: relative;
    z-index: 5;
    background-image: url("../images/icons/menu-close.svg");
  }
}

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-block: rem(24);

  @media (min-width:$mobile) {
    padding-block: rem(36);
  }

  &__logo-link {
    display: flex;
    margin-right: rem(20);

    &--fixed {
      position: relative;
      z-index: 5;
    }
  }

  &__logo {
    height: rem(38);
    transition: transform 0.3s;

    &--big {
      @media (min-width:$mobile) {
        height: rem(56);
      }
    }

    @media (any-hover: hover) {
      &:hover {
        transform: scale(1.2);
      }
    }
  }

  &__icons {
    display: flex;
    gap: rem(24);

    @media (max-width:$mobile) {
      gap: rem(32);
    }
  }
}

.menu {
  position: fixed;
  z-index: 2;
  top: 0;
  height: 100vh;
  width: 100%;
  overflow-y: auto;
  background-color: $menuBg;

  &__top {
    position: sticky;
    top: 0;
    z-index: 4;
    margin-bottom: rem(24);

    &::before {
      content: "";
      position: absolute;
      z-index: 3;
      left: 0;
      right: 0;
      height: 100%;
      background-color: $menuBg;
    }

    @media (min-width:$mobile) {
      margin-bottom: rem(8);
      padding-block: rem(46);
    }
  }

  &__body {
    font-weight: 600;
    letter-spacing: rem(2);
    text-transform: uppercase;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;
    gap: rem(32);
    margin-bottom: rem(40);
  }

  &__link {
    position: relative;
    color: $colorBg;
    font-size: rem(22);
    line-height: 1;

    @include hoverLinks($colorBg);
  }

  &__order {
    display: flex;
    flex-direction: column;
    align-items: start;
    row-gap: rem(16);
  }

  &__phone,
  &__book-ride {
    color: $colorBg;

    @include anyHover(transform, scale(1.2));
  }

  &__phone {
    line-height: math.div(27, 16);
    letter-spacing: normal;
  }

  &__book-ride {
    font-size: rem(12);
    line-height: math.div(16, 12);
    padding-bottom: rem(8);
    border-bottom: rem(1) solid $colorBg;
  }
}

.section-title {
  @include adaptiveValue("font-size", 64, 32);
  color: $white;
  font-weight: 500;
  letter-spacing: rem(-1.5);
  text-align: center;
  text-transform: capitalize;

  &--left {
    text-align-last: left;
  }
}

.subtitle-card {
  font-size: rem(24);
  font-weight: 700;
}
