.about-us {

  &__body {

    @media (min-width:$mobile) {
      @include grid;
    }
  }

  &__title {

    @media (max-width:$mobile) {
      margin-bottom: rem(32);
    }

    @media (min-width:$mobile) {
      grid-column: span 3;
    }

    @media (min-width:$tablet) {
      grid-column: span 5;
    }
  }

  &__text {
    font-size: rem(23);
    font-weight: 500;

    @media (min-width:$mobile) {
      grid-column: span 3;
    }

    @media (min-width:$tablet) {
      grid-column: span 6;
    }
  }
}
